import { debounce } from '../util/util.js';

// ÂàáÊç¢ÁÆ≠Â§¥‰∏∫ÈùôÊÄÅ HTML Ê†∑ÂºèÔºåÊó†ÈúÄÊ†πÊçÆÂõæÁâáÊï∞ÈáèÂä®ÊÄÅÁîüÊàê„ÄÇ
const carouselControl = `
<button class="carousel-control carousel-control-left carousel-control-hover">
<svg class="icon" aria-hidden="true">
    <use xlink:href="#icon-arrow-left"></use>
</svg>
</button>
<button class="carousel-control carousel-control-right carousel-control-hover">
<svg class="icon" aria-hidden="true">
    <use xlink:href="#icon-arrow-right"></use>
</svg>
</button>
`;
//ËΩÆÊí≠ÂõæÈÖçÁΩÆ
const carousel = {
    data: [],//ËΩÆÊí≠ÂõæÊï∞ÊçÆ
    currentIndex: 0,//ËΩÆÊí≠ÂõæÂΩìÂâçÂàáÊç¢ÁöÑÁîªÈù¢
    times: 2000,//ËΩÆÊí≠ÂõæÂ§öÂ∞ëÊó∂Èó¥ÂàáÊç¢ÁîªÈù¢
    animationTimes: 0.5,//ËΩÆÊí≠ÂõæÂä®ÁîªÊåÅÁª≠Êó∂Èó¥ÔºåÂçï‰Ωçs
    autoCycleTimer: new Set(),//Â¶ÇÊûúÂú®ÂàáÊç¢Âä®ÁîªÔºåÊó†Ê≥ïËøõË°åÂàáÊç¢ÁîªÈù¢
}

export function carouselRender(data) {
    //ÂàùÂßãÂåñËΩÆÊí≠Âõæ
    let carouselItem = '', carouselIndicatorsLi = '';
    const wrapper = document.querySelector('.carousel-wrapper');
    let { width = 0 } = wrapper.getBoundingClientRect();//ÂæóÂà∞ÂõæÁâáÁöÑÂÆΩÂ∫¶
    //Âä®ÊÄÅÁîüÊàêËΩÆÊí≠Âõæ
    data.forEach((item, index) => {
        //ÊåáÁ§∫Âô®ÊøÄÊ¥ªÈÄâ‰∏≠Âà§Êñ≠
        let isActive = (carousel.currentIndex == index) ? 'active' : '';
        //Âä®ÊÄÅÁîüÊàêËΩÆÊí≠ÂõæÂõæÁâáÔºåÂπ∂ÁªôÊØè‰∏ÄÂº†ÂõæÁâáÂä†‰∏äÂÅèÁßªÈáèÂíåÂä®ÁîªÊïàÊûú
        carouselItem += `
            <div class="carousel-item ${'#' + index}" style='transform:translateX(${width * (index - 1)}px);transition-duration:${carousel.animationTimes}s'>
                <img src="${item.pic}" alt="">
            </div>
            `;
        //Âä®ÊÄÅÁîüÊàêËΩÆÊí≠ÂõæÊåáÁ§∫Âô®
        carouselIndicatorsLi += `
                <li data-slide-to="${index}" class="carousel-indicators-li ${(isActive)}"></li>
            `
    });
    // ÈÄöËøáÊ®°ÊùøÂ≠óÁ¨¶‰∏≤ÔºåÊåâÁÖß home.html ‰∏≠ÁöÑ html ÁªìÊûÑËøõË°åÊéíÂ∏É
    const carouselContainer = `
        <div class="carousel-container" style="transition:transform ${carousel.animationTimes}s ">
            ${carouselControl} 
            <div class="carousel-content">   
                ${carouselItem}
            </div>
        </div>
        `;
    const carouselIndicators = `
        <ul class="carousel-indicators d-flex">
            ${carouselIndicatorsLi}
        </ul>
        `;
    // Â∞ÜÂæóÂà∞ÁöÑÂ≠óÁ¨¶‰∏≤ÈÄöËøá innerHTML ÊèíÂÖ•Âà∞ËΩÆÊí≠ÂõæÁõíÂ≠ê
    wrapper.innerHTML = carouselContainer + carouselIndicators;
    // ÈÄöËøáÂÆöÊó∂Âô®ÂºÄÂêØËá™Âä®ËΩÆÊí≠,ÊØèËøá‰∏ÄÊÆµÊó∂Èó¥Ë∞ÉÁî® getNext ÊñπÊ≥ï
    let timer = setInterval(getNext, carousel.times);
    carousel.autoCycleTimer.add(timer);
};

function getPrev() {
    // Ëé∑ÂèñÂà∞ËΩÆÊí≠ÂõæÊØè‰∏ÄÈ°πÁöÑÂõæÁâáÂÆπÂô®
    const carouselItems = document.getElementsByClassName('carousel-item');
    let length = carouselItems.length;
    // ÂΩìÂêéÈÄÄÂà∞Á¨¨‰∏ÄÂº†Êó∂ÔºåÈáçÁΩÆ‰∏∫ÊÄªÈïøÂ∫¶ÔºåÈò≤Ê≠¢indexÂèò‰∏∫Ë¥üÊï∞ÂØºËá¥bug
    carousel.currentIndex == 0 && (carousel.currentIndex = length);
    // ÊØèË∞ÉÁî®‰∏ÄÊ¨° getPrevÔºåÂ∫èÂè∑-1
    let index = carousel.currentIndex = --carousel.currentIndex % length;
    // Â∞ÜÁ±ªÊï∞ÁªÑËΩ¨Âèò‰∏∫Êï∞ÁªÑ
    let newArr = Array.from(carouselItems);
    // ËÆ°ÁÆóÂæóÂà∞ËΩÆÊí≠ÂõæÊØè‰∏ÄÈ°πÁöÑÂõæÁâáÂÆπÂô®ÁöÑÂÆΩÂ∫¶
    let { width = 0 } = getElementRect(carouselItems[0]);
    // ËΩÆÊí≠ÂõæÊï∞ÁªÑÁßªÂä®
    newArr = [...newArr.slice(index), ...newArr.slice(0, index)];
    newArr.forEach((item, i) => {// ËΩÆÊí≠ÂõæÊï∞ÁªÑÁ¨¨‰∏ÄÈ°πÁßªÂä®Âà∞ÊúÄÂêé‰∏ÄÈ°πÔºåÂÖ∂‰ªñÈ°πÈ°∫Â∫è‰∏çÂèò
        if (i == 0) { 
            item.style.transform = `translateX(${width * (length - 1)}px)`;
            item.style.opacity = 0;
        }
        item.style.transform = `translateX(${width * (i - 1)}px)`;
       item.style.opacity = 1;
    });
    // ÊåáÁ§∫Âô®ÁßªÂä®
    indicatorsRender(index);
}

function getNext() {
    const carouselItems = document.getElementsByClassName('carousel-item');
    let length = carouselItems.length;
    let index = carousel.currentIndex = ++carousel.currentIndex % length;

    let newArr = Array.from(carouselItems);
    let lens = newArr.length;
     let { width = 0 } = getElementRect(carouselItems[0]);
    //ÂΩìindex‰∏∫0Êó∂ËΩÆÊí≠ÂõæÊï∞ÁªÑ‰∏çÂÅöÂ§ÑÁêÜÔºå>0Êó∂ËøõË°åÊï∞ÁªÑÊØè‰∏ÄÈ°πÁßªÂä®
    index != 0 && (newArr = [...newArr.slice(-index, lens), ...newArr.slice(0, lens - index)]);

    newArr.forEach((item, i) => {
        if (i == 0) {// Âõ†‰∏∫ÂêëÂè≥ÁßªÂä®ÔºåËΩÆÊí≠ÂõæÊï∞ÁªÑÊúÄÂêé‰∏ÄÈ°πÁßªÂä®Âà∞Á¨¨‰∏ÄÈ°πÔºåÂÖ∂‰ªñÈ°πÈ°∫Â∫è‰∏çÂèò
            item.style.transform = `translateX(${-width * (length - 1)}px)`;
            item.style.opacity = 0;
        }
        item.style.transform = `translateX(${width * (i - 1)}px)`;
        item.style.opacity = 1;
    });

    indicatorsRender(index)
}

function indicatorsRender(index) {
    // Ëé∑ÂèñÂà∞ËΩÆÊí≠ÂõæÊØè‰∏ÄÈ°πÁöÑÊåáÁ§∫Âô®
    const indicators = document.getElementsByClassName('carousel-indicators-li');
    Array.from(indicators).forEach((item, i) => {
        if (index == i) { // ÂΩì index ÂíåÊåáÁ§∫Âô®‰∏ãÊ†áÁõ∏ÂêåÊ∑ªÂä†activeÁ±ª
            item.setAttribute('class', 'carousel-indicators-li active')
        } else {
            item.setAttribute('class', 'carousel-indicators-li')
        }
    })
}

function getElementRect(ele) {
    try { //ÈúÄË¶ÅË¢´ÊâßË°åÁöÑËØ≠Âè•
        return ele.getBoundingClientRect();
    } catch (error) { //Â¶ÇÊûúÂú®tryÂùóÈáåÊúâÂºÇÂ∏∏Ë¢´ÊäõÂá∫Êó∂ÊâßË°åÁöÑËØ≠Âè•
        /* È°µÈù¢ÈÄÄÂá∫ ele ‰∏∫Á©∫ÔºåÊ∏ÖÈô§ÂÆöÊó∂Âô®ÔºåÈò≤Ê≠¢Êä•Èîô */
        clearAllTimer();
        return {}
    }
}

function leftHandle() {//Â∑¶ÂàáÊç¢ÁÆ≠Â§¥‰∫ã‰ª∂Â§ÑÁêÜ
    //Ê∏ÖÁ©∫ÂÆöÊó∂Âô®ÊöÇÂÅúËΩÆÊí≠
    clearAllTimer()
    //ÂàáÊç¢Âà∞Ââç‰∏ÄÂº†
    getPrev();
    //ÂºÄÂêØÂÆöÊó∂Âô®ÁªßÁª≠ËΩÆÊí≠ÔºåÂπ∂Â∞ÜÂÆöÊó∂Âô®Âä†ÂÖ•Âà∞ÂÆöÊó∂Âô®‰øùÂ≠òÂô®‰∏≠
    let timer = setInterval(getNext, carousel.times);
    carousel.autoCycleTimer.add(timer)
}

function rightHandle() {//Âè≥ÂàáÊç¢ÁÆ≠Â§¥‰∫ã‰ª∂Â§ÑÁêÜ
    clearAllTimer()
    getNext();
    let timer = setInterval(getNext, carousel.times);
    carousel.autoCycleTimer.add(timer)
}

//ÂáΩÊï∞Èò≤Êäñ
const leftHandleDebounce = debounce(leftHandle, 500);
const rightHandleDebounce = debounce(rightHandle, 500);

export function initCarouselEvent() {
    const leftControl = document.getElementsByClassName('carousel-control-left');
    const rightControl = document.getElementsByClassName('carousel-control-right');
    const carouselContainer = document.querySelector('.carousel-container');
    const indicatorsWrapper = document.querySelector('.carousel-indicators');
    // Â∑¶Âè≥ÁÆ≠Â§¥ÂàáÊç¢‰∫ã‰ª∂
    leftControl[0].addEventListener('click', leftHandleDebounce);
    rightControl[0].addEventListener('click', rightHandleDebounce);
    
    // ÁßªÂÖ•ÁßªÂá∫ÊéßÂà∂ËΩÆÊí≠Êí≠Êîæ‰∫ã‰ª∂ 
    carouselContainer.addEventListener('mouseenter', () => {
        //ÁßªÂÖ•ËΩÆÊí≠ÂõæÈÄöËøáÁßªÈô§ÂÆöÊó∂Âô®ËææÂà∞ËΩÆÊí≠ÂõæÊöÇÂÅúÁöÑÁõÆÁöÑ
        clearAllTimer()
    });
    
    carouselContainer.addEventListener('mouseleave', () => {
        //ÁßªÂá∫ËΩÆÊí≠ÂõæÈÄöËøáËÆæÁΩÆÂÆöÊó∂Âô®ËææÂà∞ÂºÄÂêØËΩÆÊí≠ÂõæËΩÆÊí≠ÁöÑÁõÆÁöÑ
        let timer = setInterval(getNext, carousel.times);
        carousel.autoCycleTimer.add(timer)
    });
	//ÊåáÁ§∫Âô®‰∫ã‰ª∂Â§ÑÁêÜÂáΩÊï∞ÔºöÈÄöËøá‰∫ã‰ª∂ÂßîÊâòÂà∞Áà∂Á∫ßÂÆπÂô® ulÔºåÂáèÂ∞ëÂØπÊØè‰∏™ÊåáÁ§∫Âô®Ê∑ªÂä†‰∫ã‰ª∂ÁõëÂê¨
    indicatorsWrapper.addEventListener('mouseenter', (e) => {
        if (e.target.tagName === 'LI') {
            clearAllTimer()
            // ÂæóÂà∞ÊØè‰∏™ÊåáÁ§∫Âô®ÁöÑÂ∫èÂè∑
            const index = e.target.getAttribute('data-slide-to');
            // Â∫èÂè∑-1ÔºåË∞ÉÁî®getNext‰ºö+1Ôºå‰∏§ËÄÖÁõ∏ÊäµÊ∂àÔºåÊ†πÊçÆÂ∫èÂè∑ÊåáÂÆöÂà∞ÂØπÂ∫îÁöÑÂõæÁâá
            carousel.currentIndex = index - 1;
            getNext();
            let timer = setInterval(getNext, carousel.times);
            carousel.autoCycleTimer.add(timer)
        }
    }, true)
}

function clearAllTimer() {
    for (const i of carousel.autoCycleTimer) {
        clearInterval(i);
        if(carousel.autoCycleTimer>100){
            carousel.autoCycleTimer.clear();
        }
    }
}


