export function debounce(fn, times, isImmediately = true) {
    //Èò≤ÊäñÂáΩÊï∞
    let timer = null;
    let cb;
    if (isImmediately) {
        cb = function (...args) {
            timer && clearTimeout(timer);
            let isDone = !timer;
            timer = setTimeout(() => {
                timer = null
            }, times);
            isDone && fn.apply(this, args)
        }
    } else {
        cb = function (...args) {
            const content = this;
            timer && clearTimeout(timer)
            timer = setTimeout(() => {
                fn.apply(content, args);
            }, times);
        }
    }
    return cb
}
/**
 * @description: Ë∑ØÁî±ÂèÇÊï∞ÊèêÂèñ
 * @param {*} hash
 * @return {*}
 */
 export function getRouterOptions(hash) {
	const options = {//Ë∑ØÁî±ÈÖçÁΩÆÈÄâÈ°π
		name: '',
		params: '',
		query: '',
	};
	if (!hash || hash == '#/home') {
		options.name = 'home';
	} else {
		// ÊèêÂèñname params query‰ø°ÊÅØ
		//         0   1   2      
		//<a href='#/name/:params?query1=value1?query2=value2'></a>
		try {
			const routerArr = hash.slice(1).split('/');
			options.name = routerArr[1];
			const paramsArr = routerArr[2].split('?');
			options.params = paramsArr[0].slice(1);
			options.query = paramsArr.slice(1);
		} catch (error) {
			options.name = '404'
		}

	}
	return options;
}

/**
 * @description: Êó•ÊúüÊ†ºÂºèÂåñÂáΩÊï∞ xxxx-xx-xx
 * @param {*} time
 * @return {*}
 */
 export function formatCreateTime(time) {
	let now = new Date(time)
	let year = now.getFullYear();
	let month = now.getMonth();
	let day = now.getDay();
	return `${year}-${month}-${day}`
}

/**
 * @description: Ê≠åÊõ≤Êó∂Èó¥Ê†ºÂºèÂåñÂáΩÊï∞ 00:00
 * @param {*} time
 * @return {*}
 */
export function formatSongTime(time) {
	let second = Math.floor(time / 1000 % 60);
	second = second < 10 ? (`0${second}`) : second;
	let minute = Math.floor(time / 1000 / 60);
	minute = minute < 10 ? (`0${minute}`) : minute;
	return `${minute}:${second}`;

}
/**
 * @description: Ê≠åÊõ≤ÂàóË°®ÂéªÈô§ÈáçÂ§ç
 * @param {*} songList ÈúÄË¶ÅÊ∑ªÂä†ÁöÑÂàóË°®
 * @return {*}
 */
 export function songListFilter(songList) {
	let songListArr = JSON.parse(window.localStorage.getItem('songList')) || [];
	let addToList = songList.map(item => ({
		id: item.id,
		name: item.name,
		ar: item.ar,
		dt:item.dt,
	}));
	//ÂéªÈáç
	songListArr = [...addToList, ...songListArr];
	const tempMap = new Map();
	songListArr.forEach(item => {
		!tempMap.has(item.id) && tempMap.set(item.id, item)
	});
	// ÈôêÂà∂Êï∞Èáè
	const limitArr = [...tempMap.values()];
	(limitArr.length > 100) && (limitArr = limitArr.slice(limitArr.length - 100))
	return limitArr;
}

/**
 * @description: È´òÊñØÊ®°Á≥äÂáΩÊï∞
 * @param {*} imgData
 * @return {*}
 */
 function gaussBlur(imgData) {
	var pixes = imgData.data;
	var width = imgData.width;
	var height = imgData.height;
	var gaussMatrix = [],
		gaussSum = 0,
		x, y,
		r, g, b, a,
		i, j, k, len;

	var radius = 10;
	var sigma = 5;//Ê†áÂáÜÂ∑ÆœÉÔºåœÉÊèèËø∞Ê≠£ÊÄÅÂàÜÂ∏ÉËµÑÊñôÊï∞ÊçÆÂàÜÂ∏ÉÁöÑÁ¶ªÊï£Á®ãÂ∫¶ÔºåœÉË∂äÂ§ßÔºåÊï∞ÊçÆÂàÜÂ∏ÉË∂äÂàÜÊï£ÔºåœÉË∂äÂ∞èÔºåÊï∞ÊçÆÂàÜÂ∏ÉË∂äÈõÜ‰∏≠

	a = 1 / (Math.sqrt(2 * Math.PI) * sigma);
	b = -1 / (2 * sigma * sigma);
	//ÁîüÊàêÈ´òÊñØÁü©Èòµ
	for (i = 0, x = -radius; x <= radius; x++, i++) {
		g = a * Math.exp(b * x * x);//Ê†áÂáÜÊ≠£ÊÄÅÂàÜÂ∏ÉÂáΩÊï∞ÔºåÊ≠£ÊÄÅÂàÜÂ∏ÉËÆ∞‰ΩúN(Œº,œÉ2)ÔºåÂÖ∂‰∏≠Œº = 0 ÔºåœÉ2 = 5
		gaussMatrix[i] = g;
		gaussSum += g;

	}
	//ÂΩí‰∏ÄÂåñ, ‰øùËØÅÈ´òÊñØÁü©ÈòµÁöÑÂÄºÂú®[0,1]‰πãÈó¥
	for (i = 0, len = gaussMatrix.length; i < len; i++) {
		gaussMatrix[i] /= gaussSum;
	}
	//x ÊñπÂêë‰∏ÄÁª¥È´òÊñØËøêÁÆó
	for (y = 0; y < height; y++) {
		for (x = 0; x < width; x++) {
			r = g = b = a = 0;
			gaussSum = 0;
			for (j = -radius; j <= radius; j++) {
				k = x + j;
				if (k >= 0 && k < width) {//Á°Æ‰øù k Ê≤°Ë∂ÖÂá∫ x ÁöÑËåÉÂõ¥
					//r,g,b,a Âõõ‰∏™‰∏ÄÁªÑ
					i = (y * width + k) * 4;
					r += pixes[i] * gaussMatrix[j + radius];
					g += pixes[i + 1] * gaussMatrix[j + radius];
					b += pixes[i + 2] * gaussMatrix[j + radius];
					// a += pixes[i + 3] * gaussMatrix[j];
					gaussSum += gaussMatrix[j + radius];
				}
			}
			i = (y * width + x) * 4;
			// Èô§‰ª• gaussSum ÊòØ‰∏∫‰∫ÜÊ∂àÈô§Â§Ñ‰∫éËæπÁºòÁöÑÂÉèÁ¥†, È´òÊñØËøêÁÆó‰∏çË∂≥ÁöÑÈóÆÈ¢ò
			// console.log(gaussSum)
			pixes[i] = r / gaussSum;
			pixes[i + 1] = g / gaussSum;
			pixes[i + 2] = b / gaussSum;
			// pixes[i + 3] = a ;
		}
	}
	//y ÊñπÂêë‰∏ÄÁª¥È´òÊñØËøêÁÆó
	for (x = 0; x < width; x++) {
		for (y = 0; y < height; y++) {
			r = g = b = a = 0;
			gaussSum = 0;
			for (j = -radius; j <= radius; j++) {
				k = y + j;
				if (k >= 0 && k < height) {//Á°Æ‰øù k Ê≤°Ë∂ÖÂá∫ y ÁöÑËåÉÂõ¥
					i = (k * width + x) * 4;
					r += pixes[i] * gaussMatrix[j + radius];
					g += pixes[i + 1] * gaussMatrix[j + radius];
					b += pixes[i + 2] * gaussMatrix[j + radius];
					// a += pixes[i + 3] * gaussMatrix[j];
					gaussSum += gaussMatrix[j + radius];
				}
			}
			i = (y * width + x) * 4;
			pixes[i] = r / gaussSum;
			pixes[i + 1] = g / gaussSum;
			pixes[i + 2] = b / gaussSum;
		}
	}
	//end
	return imgData;
}

