export function blur(ele, src) {
	// const canvas = document.querySelector('#canvas');
	//ËÆæÁΩÆ canvas ÁîªÂ∏É
	const canvas = document.createElement('canvas');
	canvas.width = 100;
	canvas.height = 100;
	// Ëé∑Âæó canvas ‰∏ä‰∏ãÊñá
	const ctx = canvas.getContext('2d');
	//Image()ÂáΩÊï∞Â∞Ü‰ºöÂàõÂª∫‰∏Ä‰∏™Êñ∞ÁöÑHTMLImageElementÂÆû‰æã„ÄÇÂÆÉÁöÑÂäüËÉΩÁ≠â‰ª∑‰∫é document.createElement('img')
	const img = new Image();
	img.src = src;
	// Ê∑ªÂä†Â±ûÊÄßcrossoriginÔºåÂ¢ûÂä†imgÂÖÉÁ¥†ÁöÑË∑®ÂüüÊîØÊåÅÔºåËÆæÁΩÆÂÄº‰∏∫anonymousÔºåË°®Á§∫ÂØπÊ≠§ÂÖÉÁ¥†ÁöÑ CORS ËØ∑Ê±ÇÂ∞Ü‰∏çËÆæÁΩÆÂá≠ÊçÆÊ†áÂøó
	img.setAttribute('crossorigin', 'anonymous');
	//ÂΩìimgÂÖÉÁ¥†Âä†ËΩΩÂÆåÊàêÊó∂ÊâßË°å
	img.onload = function () {
		const { width, height } = canvas;
		// ctx.drawImage(img,0,0,img.width,img.height,5,5,width,height);

		ctx.drawImage(img, 0, 0, img.width, img.height, 0, 0, width, height);
		let imgData = ctx.getImageData(0, 0, width, height);
		let gaussData = gaussBlur(imgData);
		ctx.putImageData(gaussData, 0, 0);
		//Â∞ÜÂõæÁâáËΩ¨Âèò‰∏∫base64ÁöÑÁºñÁ†ÅÊ†ºÂºè
		let imgSrc = canvas.toDataURL();
		//ËÆæÁΩÆËÉåÊôØÂõæÁâá
		ele.style.backgroundImage = 'url(' + imgSrc + ')';
	}
}
